import $ from 'jquery';
import { modalClose } from './modal';
export function ajaxRequest(type, url, data, source, onsuccess, onerror, onfinish) {
    if (source) {
        if (!source.hasClass('noanim')) {
            $(source).replaceWith("<img alt=\"Laden\" id=\"" + source.attr('id') + "\" title=\"" + url + "\" src=\"/images/loading-arrows.gif\" />");
            source = $("img[title=\"" + url + "\"]");
        }
        else if (source.hasClass('InlineForm')) {
            $(source).find('.FormElement:first').css({
                'background-image': 'url("/images/loading-fb.gif")',
                'background-position': 'center right',
                'background-repeat': 'no-repeat',
            });
        }
        else {
            source.addClass('loading');
        }
    }
    var contentType = 'application/x-www-form-urlencoded; charset=UTF-8';
    var processData = true;
    if (data instanceof FormData) {
        contentType = false;
        processData = false;
    }
    $.ajax({
        cache: false,
        contentType: contentType,
        data: data,
        processData: processData,
        type: type,
        url: url,
    }).done(function (response) {
        if (source) {
            if (!$(source).hasClass('noanim')) {
                $(source).hide();
            }
            else if ($(source).hasClass('InlineForm')) {
                $(source).find('.FormElement:first').css({
                    'background-image': '',
                    'background-position': '',
                    'background-repeat': '',
                });
            }
            source.removeClass('loading');
        }
        onsuccess(response);
    }).fail(function (response, textStatus, errorThrown) {
        if (errorThrown === '') {
            errorThrown = 'Nog bezig met laden!';
        }
        if (source) {
            $(source).replaceWith('<img alt="Mislukt" title="' + errorThrown + '" src="/plaetjes/famfamfam/cancel.png" />');
        }
        else {
            modalClose();
        }
        if (onerror) {
            if (response.responseText.startsWith('<!DOC')) {
                onerror('Er ging iets fout, code is: ' + response.status);
            }
            onerror(response.responseText);
        }
    }).always(function () {
        if (onfinish) {
            onfinish();
        }
    });
}
export function ketzerAjax(url, ketzer) {
    $(ketzer + ' .aanmeldbtn').addClass('loading');
    $.ajax({
        cache: false,
        data: '',
        type: 'GET',
        url: url,
    }).done(function (data) {
        $(ketzer).replaceWith(data);
    }).fail(function (jqXHR, textStatus, errorThrown) {
        $(ketzer + ' .aanmeldbtn')
            .replaceWith($("<div class=\"alert alert-danger\"><strong>Actie mislukt!</strong> " + errorThrown + "</div>"));
        alert(jqXHR.responseText);
    });
    return true;
}
