import axios from 'axios';
import $ from 'jquery';
import { ajaxRequest } from './ajax';
import { bbCodeSet } from './bbcode-set';
import { domUpdate } from './context';
import ctx, { init } from './ctx';
import { fnGetSelection, fnUpdateDataTable } from './datatable/api';
import { modalClose, modalOpen } from './modal';
import { redirect, reload } from './util';
ctx.addHandlers({
    '.InlineFormToggle': function (el) { return el.addEventListener('click', function (event) { return formToggle(el, event); }); },
    '.SubmitChange': function (el) { return el.addEventListener('change', formSubmit); },
    '.cancel': function (el) { return el.addEventListener('click', formCancel); },
    '.reset': function (el) { return el.addEventListener('click', formReset); },
    '.submit': function (el) { return el.addEventListener('click', formSubmit); },
    'form.Formulier': function (el) { return $(el).on('submit', formSubmit); },
    'textarea.BBCodeField': function (el) { return $(el).markItUp(bbCodeSet); },
    'time.timeago': function (el) { return $(el).timeago(); },
});
export function formIsChanged(form) {
    var changed = false;
    $(form).find('.FormElement').not('.tt-hint').each(function () {
        var elmnt = $(this);
        if (elmnt.is('input:radio')) {
            if (elmnt.is(':checked') && elmnt.attr('origvalue') !== elmnt.val()) {
                changed = true;
                return false;
            }
        }
        else if (elmnt.is('input:checkbox')) {
            if (elmnt.is(':checked') !== (elmnt.attr('origvalue') === '1')) {
                changed = true;
                return false;
            }
        }
        else if (elmnt.val() !== elmnt.attr('origvalue')) {
            changed = true;
            return false;
        }
    });
    return changed;
}
export function formInlineToggle(form) {
    form.prev('.InlineFormToggle').toggle();
    form.toggle();
    form.children(':first').trigger('focus');
}
export function formToggle(target, event) {
    event.preventDefault();
    var form = $(target).next('form');
    formInlineToggle(form);
    return false;
}
export function formReset(event, form) {
    if (!form) {
        form = $(event.target).closest('form');
        event.preventDefault();
    }
    if ($(event.target).hasClass('confirm') && !confirm($(event.target).attr('title') + '.\n\nWeet u het zeker?')) {
        return false;
    }
    form.find('.FormElement').each(function () {
        var orig = $(event.target).attr('origvalue');
        if (typeof orig === 'string') {
            $(this).val(orig);
        }
    });
    return false;
}
export function formSubmit(event) {
    var target = event.target;
    var $target = $(target);
    if ($target.hasClass('confirm')) {
        var q = $target.attr('title');
        if (q) {
            q += '.\n\n';
        }
        else {
            q = 'Weet u het zeker?';
        }
        if (!confirm(q)) {
            event.preventDefault();
            return false;
        }
    }
    var form = $target.closest('form');
    if (!form.hasClass('Formulier')) {
        return false;
    }
    if (form.hasClass('PreventUnchanged') && !formIsChanged(form)) {
        event.preventDefault();
        alert('Geen wijzigingen');
        return false;
    }
    if ($target.attr('href')) {
        form.attr('action', $target.attr('href'));
    }
    if (!(form.hasClass('ModalForm') || form.hasClass('InlineForm'))) {
        form.off('submit');
        form.trigger('submit');
        return true;
    }
    else {
        event.preventDefault();
        var formData_1 = new FormData(form.get(0));
        var done = domUpdate;
        var source = false;
        if (form.hasClass('InlineForm')) {
            source = form;
            formData_1.append('InlineFormId', form.attr('id'));
            if (form.data('submitCallback')) {
                done = form.data('submitCallback');
            }
        }
        if (form.hasClass('ModalForm')) {
            done = function (response) {
                if (typeof response === 'string') {
                    domUpdate(response);
                }
                else {
                    modalClose();
                }
            };
        }
        if (form.hasClass('DataTableResponse')) {
            var tableId_1 = form.attr('data-tableid');
            if (!document.getElementById(tableId_1)) {
                alert('DataTable not found');
            }
            formData_1.append('DataTableId', tableId_1);
            var selection = fnGetSelection('#' + tableId_1);
            $.each(selection, function (key, value) {
                formData_1.append('DataTableSelection[]', value);
            });
            done = function (response) {
                if (typeof response === 'object') {
                    fnUpdateDataTable('#' + tableId_1, response);
                    if (response.modal) {
                        modalOpen(response.modal);
                        init(document.querySelector('#modal'));
                    }
                    else {
                        modalClose();
                    }
                }
                else {
                    domUpdate(response);
                }
            };
            if (!form.hasClass('noanim')) {
                source = false;
            }
        }
        if (form.hasClass('ReloadPage')) {
            done = reload;
        }
        else if (form.hasClass('redirect')) {
            done = redirect;
        }
        ajaxRequest('POST', form.attr('action'), formData_1, source, done, alert, function () {
            if (form.hasClass('SubmitReset')) {
                formReset(event, form);
            }
        });
        return false;
    }
}
export function formCancel(event) {
    var source = $(event.target);
    if (source.hasClass('confirm') && !confirm(source.attr('title') + '.\n\nWeet u het zeker?')) {
        event.preventDefault();
        return false;
    }
    var form = source.closest('form');
    if (form.hasClass('InlineForm')) {
        event.preventDefault();
        formInlineToggle(form);
        return false;
    }
    if (form.hasClass('ModalForm')) {
        event.preventDefault();
        var href = source.attr('href');
        if (href) {
            axios.get(href);
        }
        if (!formIsChanged(form) || confirm('Sluiten zonder wijzigingen op te slaan?')) {
            modalClose();
        }
        return false;
    }
    return true;
}
