import { formatBedrag, formatFilesize } from '../util';
import { getApiFromSettings } from './api';
export default {
    default: function (data, type) {
        if (data === null || typeof data !== 'object') {
            return data;
        }
        switch (type) {
            case 'sort':
                return data.sort;
            case 'export':
                return data.export;
            default:
                return data.display;
        }
    },
    bedrag: function (data) {
        return formatBedrag(data);
    },
    check: function (data) {
        return '<span class="ico ' + (data ? 'tick' : 'cross') + '"></span>';
    },
    aanmeldFilter: function (data) {
        return data ? "<span class=\"ico group_key\" title=\"Aanmeld filter actief: '" + data + "'\"></span>" : '';
    },
    aanmeldingen: function (data, type, row) {
        return row.aantal_aanmeldingen + ' (' + row.aanmeld_limiet + ')';
    },
    totaalPrijs: function (data, type, row) {
        return formatBedrag(row.aantal_aanmeldingen * parseInt(row.prijs, 10));
    },
    timeago: function (data, type, row, meta) {
        var api = getApiFromSettings(meta.settings);
        var cell = api.cell(meta.row, meta.col).node().firstChild;
        switch (type) {
            case 'sort':
            case 'export':
                return cell.dateTime;
            default:
                return data;
        }
    },
    filesize: function (data, type) {
        switch (type) {
            case 'sort':
                return Number(data);
            default:
                return formatFilesize(data);
        }
    },
};
