import axios from 'axios';
import $ from 'jquery';
import { bbvideoDisplay } from './bbcode';
import { init } from './ctx';
import './formulier';
import './fotoalbum/FotoAlbum';
require('lightbox2');
require('./lib/jquery.markitup');
require('jquery-ui/ui/widgets/tooltip');
require('jquery-hoverintent');
require('timeago');
window.bbcode = {
    bbvideoDisplay: bbvideoDisplay,
};
var $window = $(window);
var $header = $('#header');
var $banner = $('#banner');
if (typeof $banner[0] === 'undefined') {
    $banner = $('#banner-small');
}
$window.on('load', function () {
    setTimeout(function () {
        $('div.bb-img-loading').each(function () {
            var content = $(document.createElement('img'));
            content.on('error', function () {
                $(this).attr('title', 'Afbeelding bestaat niet of is niet toegankelijk!');
                $(this).attr('src', '/plaetjes/famfamfam/picture_error.png');
                $(this).css('width', '16px');
                $(this).css('height', '16px');
                $(this).removeClass('bb-img-loading').addClass('bb-img');
            });
            content.addClass('bb-img');
            content.attr('alt', $(this).attr('title'));
            content.attr('style', $(this).attr('style'));
            content.attr('src', $(this).attr('src'));
            $(this).html(content.toString());
            content.on('load', function () {
                var foto = content.attr('src').indexOf('/plaetjes/fotoalbum/') >= 0;
                var video = $(this).parent().parent().hasClass('bb-video-preview');
                var hasAnchor = $(this).closest('a').length !== 0;
                $(this).parent().replaceWith($(this));
                if (!foto && !video && !hasAnchor) {
                    $(this).wrap("<a class=\"lightbox-link\" href=\"" + $(this).attr('src') + "\" data-lightbox=\"page-lightbox\"></a>");
                }
            });
        });
    });
});
var lazyLoad = (function () {
    var hasLoaded = false;
    return function () {
        if (hasLoaded) {
            return;
        }
        hasLoaded = true;
        setTimeout(function () {
            $('.lazy-load').each(function () {
                $(this).replaceWith(this.textContent);
            });
        });
    };
})();
$window.on('scroll', function () {
    if ($(window).scrollTop() > 0) {
        lazyLoad();
    }
    if (window.pageYOffset > $banner.outerHeight()) {
        $header.removeClass('alt');
    }
    else {
        $header.addClass('alt');
    }
});
$window.on('resize', function () { return $window.trigger('scroll'); });
$window.trigger('scroll');
init(document.body);
var contactForm = document.querySelector('#contact-form');
if (contactForm) {
    var errorContainer_1 = document.querySelector('#melding');
    var submitButton_1 = contactForm.submitButton;
    contactForm.addEventListener('submit', function (event) {
        event.preventDefault();
        errorContainer_1.innerHTML = '';
        submitButton_1.disabled = true;
        var formData = new FormData(contactForm);
        axios.post('/contactformulier/interesse', formData)
            .then(function (response) {
            contactForm.reset();
            submitButton_1.disabled = false;
            errorContainer_1.innerHTML = '<div class="alert alert-success">' +
                '<span class="ico accept"></span>' + response.data +
                '</div>';
        })
            .catch(function (error) {
            submitButton_1.disabled = false;
            errorContainer_1.innerHTML = '<div class="alert alert-danger">' +
                '<span class="ico exclamation"></span>' + error.response.data +
                '</div>';
        });
        return false;
    });
}
